body {
  background-color:#e1e1e1;
}
* {
  margin:0;
  padding:0;
}
.header {
  height:50px;
  width:100%;
  background-color: #444444;
  margin-bottom:20px;
}
.navbarContainer {
  height:100%;
  width:90%;
  margin:0 auto;

}

.brand {
  display:inline-block;
  width:95px;
  height:auto;
  top:90%;
}
.brand img {
  margin-top:9px;
  display:inline-block;
}
.navbar-search {
  /* height:20px; */
  display:inline-block;
  width:20%;
  margin-left:3%;
}

.navbar-search input {
  height:25px;
  width:100%;
  border-radius:10px;
  border:none;
  background-color:#888888;
  /* background-image:url("https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2026677895,4217771588&fm=26&gp=0.jpg");
  background-position:left;
  background-repeat:no-repeat;
  background-size:20px; */
  transition:background-color 0.5s;
}
.navbar-search input:hover {
  background-color: #ffff;
}

.nav {
  /* margin-top:9px; */
  height:50px;
  display:inline-block;
  float:right;
}
.header ul {
  list-style:none;
}
.header ul li {
  line-height:50px;
  float:left;
  margin-left:25px;
}
.link {
  /* line-height:30px; */
  color:#b4b2b2;
  text-decoration:none;
  transition:color 0.3s;
}
.link:hover {
  color:white;
}
