.passage-list {
  border-bottom:1px solid #f9f9f9;
  height:50px;
  overflow: hidden;
  background-color:#ffffff;
}
.passage-list p {
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  line-height:50px;
  margin-left:10px;
  text-decoration:none;
}
.passage-list a p{
  text-decoration:none;
}
.passage-list a {
  text-decoration:none;
  color:black;
}

.passage-list img {
  display:inline-block;
  margin-left:10px;
  margin-top:15px;
  margin-right:10px;
  float:left;
  width:20px;
  height:20px;
}

.page-nav {
  background-color: white;
}
.page-nav ul{
  list-style:none;
  margin-top:20px;
  margin-left:40px;
  margin-bottom:40px;
  padding-bottom:40px;
}
.page-nav li {
  display:inline-block;
  width:30px;
  height:30px;
  background-color:white;
  border:1px solid #A9A9A9;
  text-align:center;

}
