.sideBar {
  width:300px;
  height:1000px;
  float:right;
  margin-right:10%;
}

.pannel {
  width:100%;
  background-color: white;
  margin-bottom:10px;
}
.inner {
  padding-top:10px;
  padding-bottom:10px;
  width:90%;
  margin:0 auto;
}


.pannel1-p2 {
  margin-top:5px;
  font-size:15px;
}
.pannel1-p2 a {
  text-decoration:none;
}

.gitLogin {
  width:150px;
  height:35px;
  margin-top:9px;
  border:none;
  background-color:#5bc0de;
  color:#fff;
  font-size:15px;
  transition:background-color 0.5s;
}
.gitLogin:hover {
  background-color:#5ba0de;
}

.pannel img {
  width:100%;
  margin-bottom:5px;
}
.pannel img:nth-child(3) {
  margin-bottom:0px;
}


.no-reply-problem {
  height:30px;
  background-color:#f6f6f6 ;
}

.no-reply-problem p {
  font-size:15px;
  line-height:30px;
  padding-left:10px;
}


.no-reply-list a {
  margin-bottom:5px;
  display:block;
  font-size:14px;
  color:#a8a8a1;
}

.friendship {
  width:60%;
  /* text-align:left; */
  margin-left:15px;
}
.qr-code img {
  width:80%;
  display:inline-block;
  margin:0 auto;
}
.qr-code {
  text-align:center;
}

@media screen and (max-width:800px) {
  .sideBar {
    display:none;
    transition:display 0.3s;
  }
}
